<template>
  <div class="form-elements">
    <div class="va-row">
      <div class="flex md12">
        <vuestic-widget :headerText="'orders.newOrder.newOrderTitle' | translate">
          <!--Need fo add order CODE to the header-->
          <form>

            <div class="va-row">

              <div class="flex md12">
                <div class="form-group">
                  <div class="input-group">
                  <textarea type="text" id="document-name"
                            required></textarea>
                    <label class="control-label" for="document-name">{{'orders.newOrder.documentName'
                      | translate}}</label><i class="bar"></i>
                  </div>
                </div>
              </div>

              <div class="flex md4">
                <fieldset>
                  <vuestic-simple-select
                    :label="'orders.newOrder.agencyName' | translate"
                    v-model="agenciesModel" v-bind:options="agencies" option-key="agencyName"
                    :value="agencies.agencyId"/>

                  <div class="form-group">
                    <div class="input-group">
                      <input id="price-per-page" required v-model="pricePerPageModel" @change="totalPrice"/>
                      <label class="control-label" for="price-per-page">{{'orders.newOrder.pricePerPage'
                        | translate}}</label><i class="bar"></i>
                    </div>
                  </div>
                  <div class="form-group">
                    <div class="input-group">
                      <vuestic-date-picker
                        id="order-date"
                        v-model="orderDateModel"
                      />
                      <label class="control-label" for="order-date">
                        {{ $t('orders.newOrder.orderDate') }}
                      </label>
                      <i class="bar"></i>
                    </div>
                  </div>

                  <vuestic-simple-select
                    :label="'orders.newOrder.topic' | translate"
                    v-model="topicModel" v-bind:options="topics" option-key="topicName" :value="topics.topicId"/>


                  <div class="form-group">
                    <div class="input-group">
                      <input id="partial-payment" v-model="partialPaymentModel"/>
                      <label class="control-label" for="partial-payment">{{'orders.newOrder.partialPayment'
                        | translate}}</label><i class="bar"></i>
                    </div>
                  </div>
                </fieldset>
              </div>

              <div class="flex md4">
                <fieldset>
                  <div class="form-group">
                    <div class="input-group">
                      <input id="symbol-count" required v-model="symbolCountModel" @change="pageCalculator"/>
                      <label class="control-label" for="symbol-count">{{'orders.newOrder.symbolCount'
                        | translate}}</label><i class="bar"></i>
                    </div>
                  </div>

                  <div class="form-group">
                    <div class="input-group">
                      <input id="totalPrice" required v-model="totalPriceModel"/>
                      <label class="control-label" for="price-per-page">{{'orders.newOrder.totalPrice'
                        | translate}}</label><i class="bar"></i>
                    </div>
                  </div>

                  <div class="form-group">
                    <div class="input-group">
                      <vuestic-date-picker
                        id="readiness-time"
                        :config="{enableTime: true}"
                        v-model="readinessTimeModel"
                      />
                      <label class="control-label" for="readiness-time">
                        {{ $t('orders.newOrder.readinessTime') }}
                      </label>
                      <i class="bar"></i>
                    </div>
                  </div>

                  <vuestic-simple-select
                    :label="'orders.newOrder.manager' | translate"
                    v-model="managerModel"
                    option-key="managerName"
                    v-bind:options="managers" :value="managers.managerId"/>

                  <div class="form-group">
                    <div class="input-group">
                      <vuestic-checkbox
                        :label="$t('orders.newOrder.isPaid')"
                        v-model="isPaidModel.selected" @click="paymentDone()"/>
                    </div>
                  </div>
                </fieldset>
              </div>

              <div class="flex md4">
                <fieldset>
                  <div class="form-group">
                    <div class="input-group">
                      <input id="page-count" required v-model="pageCountModel"/>
                      <label class="control-label" for="page-count">{{'orders.newOrder.pageCount'
                        | translate}}</label><i class="bar"></i>
                    </div>
                  </div>

                  <vuestic-simple-select
                    :label="'orders.newOrder.langPair' | translate"
                    v-model="languageModel"
                    option-key="langPair"
                    v-bind:options="langPairs" :value="langPairs.pairId"/>

                  <div class="form-group">
                    <div class="input-group">
                      <input id="incoming-number" v-model="incomingNumberModel"/>
                      <label class="control-label" for="incoming-number">{{'orders.newOrder.incomingNumber'
                        | translate}}</label><i class="bar"></i>
                    </div>
                  </div>
                  <div class="form-group">
                    <div class="input-group">
                      <input id="customer-name" v-model="customerModel"/>
                      <label class="control-label" for="customer-name">{{'orders.newOrder.customer'
                        | translate}}</label><i class="bar"></i>
                    </div>
                  </div>

                  <div class="form-group">
                    <div class="input-group">
                      <vuestic-date-picker
                        id="payment-date"
                        v-model="paymentDateModel"
                      />
                      <label class="control-label" for="payment-date">
                        {{ $t('orders.newOrder.paymentDate') }}
                      </label>
                      <i class="bar"></i>
                    </div>
                  </div>
                  <div class="form-group">
                    <div class="input-group">
                      <input id="order-code" required v-model="orderCodeModel"/>
                      <label class="control-label" for="order-code">{{'orders.newOrder.orderCode'
                        | translate}}</label><i class="bar"></i>
                    </div>
                  </div>
                  <vuestic-simple-select
                    :label="'orders.newOrder.payer' | translate"
                    v-model="payerModel"
                    option-key="payerName"
                    v-bind:options="payers" :value="payers.payerId"/>
                </fieldset>
              </div>

              <div class="flex md12">
                <div class="form-group">
                  <div class="input-group">
                    <textarea type="text" id="order-comments" v-model="orderCommentsModel"></textarea>
                    <label class="control-label" for="order-comments">{{'orders.newOrder.orderComments'
                      | translate}}</label><i class="bar"></i>
                  </div>
                </div>
              </div>

            </div>
            <div class="va-row btn-margin-row">
              <div class="flex sm6 lg6 xl3 justify--center">
                <button class="btn btn-success" @click="saveOrder" type="submit">
                  {{'agencies.inputs.save' | translate}}
                </button>
              </div>
              <div class="flex sm6 lg6 xl3 justify--center">
                <button class="btn btn-secondary">
                  {{'agencies.inputs.cancel' | translate}}
                </button>
              </div>
            </div>
          </form>
        </vuestic-widget>
      </div>
    </div>
  </div>
</template>

<script>
  // import CountriesList from 'data/CountriesList'
  import {AXIOS} from '../http-common';

  var today = new Date();

  export default {
    name: 'form-elements',
    computed: {
      datePickerDisabled: () => [date => !(date.getDate() % 5)],
      isSuccessfulEmailValid() {
        let isValid = false
        if (this.formFields.successfulEmail) {
          isValid = this.formFields.successfulEmail.validated && this.formFields.successfulEmail.valid
        }
        return isValid
      },
    },
    data() {
      return {
        payerModel: '',
        customerModel: '',
        incomingNumberModel: '',
        orderCommentsModel: '',
        partialPaymentModel: '',
        orderCodeModel: '',
        payers: [],
        orderDateModel: '',
        clearableText: '',
        agencies: [],
        pageCountModel: '',
        symbolCountModel: '',
        pricePerPageModel: '',
        agenciesModel: '',
        languageModel: '',
        topicModel: '',
        totalPriceModel: '',
        managerModel: '',
        paymentDateModel: '',
        readinessTimeModel: '',
        langPairs: [],
        topics: [],
        managers: [],
        simpleSelectModel:
          '',
        multiSelectModel:
          [],
        multiSelectCountriesModel:
          [],
        radioSelectedOption:
          'option1',
        radioSelectedDisableOption:
          'option1',
        isPaidModel: {selected: false},
        checkbox:
          {
            unselected: false,
            selected:
              true,
            readonly:
              true,
            disabled:
              true,
            error:
              false,
            errorMessages:
              true
          },
        errorMessages: ['error message 1', 'error message 2'],
        datepicker:
          {
            simple: '2019-04-08 14:10',
            time:
              '2018-05-08 14:10',
            range:
              '2018-05-08 to 2018-05-23',
            disabled:
              '2018-05-09',
            multiple:
              '2018-04-25, 2018-04-27',
            customFirstDay:
              '2018-05-09',
            customDate:
              '2018-05-09',
          }
        ,
      }
    },
    methods: {
      clear(field) {
        this[field] = ''
      },
      getAgencies() {
        AXIOS.get(`api/selectAllAgencies`)
          .then(response => {
            // JSON responses are automatically parsed.
            this.agencies = response.data;
            console.log("her");
            console.log(response.data);
            console.log(response.status);
            console.log(response.statusText);
            console.log(response.headers);
            console.log(response.config);
          })
          .catch(e => {
            this.errors.push(e)
          })
      },
      getLangPairs() {
        AXIOS.get(`api/getLangPairs`)
          .then(response => {
            this.langPairs = response.data;
          })
          .catch(e => {
            this.errors.push(e)
          })
      },
      getManagers() {
        AXIOS.get(`api/getAllManagers`)
          .then(response => {
            this.managers = response.data;
          })
          .catch(e => {
            this.errors.push(e)
          })
      },
      getTopics() {
        AXIOS.get(`api/getAllTopics`)
          .then(response => {
            this.topics = response.data;
          })
          .catch(e => {
            this.errors.push(e)
          })
      },
      pageCalculator() {
        var pages = parseInt(this.pageCountModel, 10);
        var symbols = parseInt(this.symbolCountModel, 10);
        pages = (symbols / 1800).toFixed(2);
        this.pageCountModel = pages;
      },
      totalPrice() {
        var pricePerPage = parseInt(this.pricePerPageModel, 10);
        this.totalPriceModel = (pricePerPage * this.pageCountModel).toFixed(0);
      },
      paymentDone() {
        if (this.isPaidModel === true) {
          this.paymentDateModel = today;
          console.log("clicked1");
        }
        else {
          this.paymentDateModel = '';
          console.log("clicked2");
        }
      },
      orderDate() {
        this.orderDateModel = today;
      },
      saveOrder() {
        AXIOS({
          method: 'post',
          url: 'api/saveOrder',
          data: {
            orderCode: this.orderCodeModel,
            documentName: this.documentName,
            agencyId: this.agenciesModel,
            symbolCount: this.symbolCountModel,
            pageCount: this.pageCountModel,
            pricePerPage: this.pricePerPageModel,
            totalPrice: this.totalPriceModel,
            langPairId: this.languageModel,
            orderDate: this.orderDateModel,
            readinessTime: this.readinessTimeModel,
            incomingNumber: this.incomingNumberModel,
            topicId: this.topicModel,
            managerId: this.managerModel,
            customer: this.customerModel,
            partialPayment: this.partialPaymentModel,
            paymentDone: this.isPaidModel,
            paymentDate: this.paymentDateModel,
            payerId: this.payerModel,
            orderComments: this.orderCommentsModel
          }
        });
        this.launchToast();
      },
      mounted() {
        this.getAgencies();
        this.getLangPairs();
        this.getManagers();
        this.getTopics();
        this.orderDate();
      }
    }
  }
</script>
